/* src/components/PasswordPage/PasswordPage.css */
.password-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-image: url('/src/Assets/10.gif');
  background-size: cover;
  background-position: center;
  font-family: Arial, sans-serif;
}

.password-page h1 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #020202;
}

.password-page form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.password-page input {
  padding: 0.5rem;
  font-size: 1rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.password-page button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.password-page button:hover {
  background-color: #0056b3;
}

.password-page .error {
  color: red;
  margin-top: 1rem;
}